import { postJSON } from './http';

function createOrder(comboId, date) {
    return postJSON('/dc/order/add', { comboId, date });
}

function modifyOrder(orderId, comboId) {
    return postJSON('/dc/order/mod', { orderId, comboId });
}

function removeOrder(id) {
    return postJSON('/dc/order/del', { id });
}

function queryTodayOrder(day = 0) {
    return postJSON('/dc/order/getTodayOrder', { day });
}

function queryOrderList(pageNo, pageSize, status = null) {
    return postJSON('/dc/order/listCur', { pageNo, pageSize, status });
}

function queryOrderHisList(pageNo, pageSize, status = null) {
    return postJSON('/dc/order/listHis', { pageNo, pageSize, status });
}

function eveOrder(orderId, eveScore, eveDesc) {
    return postJSON('/dc/order/eveOrder', { orderId, eveScore, eveDesc });
}

export {
    eveOrder,
    createOrder,
    modifyOrder,
    removeOrder,
    queryOrderList,
    queryOrderHisList,
    queryTodayOrder
}