<template>
    <div data-view="food-list" class="ke-fs-content-view scroll" :class="{
        'show-goods-img' : !!showGoodsImg,
        'mod' : isMod }">
        <div v-if="showGoodsImg" class="ke-fs-food-list-popup" @click="_hideGoodsImg">
            <div class="ke-fs-food-popup-inner">
                <div class="ke-fs-food-big-cover">
                    <van-icon name="close" @click="_hideGoodsImg" />
                    <img :src="showGoodsImg" />
                </div>
            </div>
        </div>
        <van-swipe class="ke-fs-food-list-swipe" :autoplay="3000" :show-indicators="false">
            <van-swipe-item v-for="(image, index) in banners" :key="index" @click="isShowTips=true">
                <img :src="image" />
            </van-swipe-item>
        </van-swipe>
        <van-overlay :show="isShowTips" :z-index="1024" @click="isShowTips=false">
            <div class="ke-fs-food-list-tips">
                <h6>温馨提示:</h6>
                <ol>
                    <li>中心简餐种类包括：营养简餐、特色简餐、运动简餐、精美面食四种类型，每人每天仅可选择一种类型简餐。</li>
                    <li>实行预订制，当日可以预订次日及次日后午餐。次日午餐最迟当日下午15点前预定，超过订餐截止时间无法订餐。</li>
                    <li>可提前预定一周工作日午餐，建议提前预定，未预订人员，食堂将默认供应最多人预订套餐。</li>
                    <li>为避免浪费，各类型简餐5人（含5人）以上成团；若少于5人预订，则订餐失败，系统将在订餐当日下午16点前通过政务微信推送订餐失败修改消息，请及时查收并在1小时内完成订单修改。</li>
                    <li>就餐时，凭系统生成的订单号到食堂一楼出餐窗口取餐。</li>
                </ol>
                <van-button type="danger" size="small" block>关 闭</van-button>
            </div>
        </van-overlay>
        <van-pull-refresh v-model="isRefresh" @refresh="_onRefresh">
            <ul class="ke-fs-food-categories">
                <li v-for="(c) in categories" 
                    :key="c.type" 
                    :class="{'actived' : c.type === selectedCategory}"
                    @click="_setCategory(c.type)">
                    <img :src="c.type === selectedCategory ? c.icon.actived : c.icon.normal"  alt="c.label"/>
                    <span v-html="c.label"></span>
                </li>
            </ul>
            <div class="ke-fs-food-list">
                <template v-if="isInit">
                    <van-list v-model="isLoading" :finished="isFinished" finished-text="" :offset="40">
                        <div v-for="combo in curComboList" :key="combo.id" class="ke-fs-food-item">
                            <img 
                                :src="combo.comboImg && combo.comboImg.length > 0 ? `./common/download?fileName=${combo.comboImg}` : cover"
                                @click="_showGoodsImg(combo.comboImg)" />
                            <div class="ke-fs-food-item__summary">
                                <h4>{{combo.comboName}}</h4>
                                <ul v-if="(1 == combo.comboType || 4 == combo.comboType) && combo.goods.length > 0">
                                    <li v-for="g in combo.goods" :key="g.id">{{g.goodsName}}</li>
                                </ul>
                                <p>
                                    <span>已有<strong>{{combo.cnt}}</strong>人点餐</span>
                                </p>
                                <!-- :disabled="-1 != status && !isMod" -->
                                <van-button type="danger" icon="plus" size="mini" @click="_confrimDate(combo.id)" round>点餐</van-button>
                            </div>
                        </div>
                        <van-empty v-if="isFinished && curComboList.length == 0" :image="empty" description="今日菜品还未上架..." />
                    </van-list>
                </template>
                <div v-else class="ke-fs-food-list__loading">
                    <van-loading type="spinner">加载中,请稍后...</van-loading>
                </div>
            </div>
        </van-pull-refresh>
        <van-calendar v-model="showTime" v-bind="calendarObj" @confirm="_onUserConfFrimDate" @close="chooseComboId = null" />
    </div>
</template>

<script>
    import { queryComboList } from '@/api/combo-api';
    import { createOrder, modifyOrder, queryOrderList } from '@/api/order-api';
    import { Dialog, Toast } from 'vant';

    const COMBO_TYPE_NAMES = {
        type1 : 'jc',
        type2 : 'ms',
        type3 : 'ts',
        type4 : 'yd'
    };

    const MONTH_DAYS = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
        ONE_DAY_TIME = 1000 * 60 * 60 * 24 ;

    export default {
        data() {
            return {
                isInit           : false,
                isMod            : false,
                modDate          : null,
                isRefresh        : false,
                isLoading        : false,
                isFinished       : false,
                isShowTips       : false,
                selectedCategory : COMBO_TYPE_NAMES.type1,
                categories       : [{
                    type  : COMBO_TYPE_NAMES.type1,
                    label : '营养<br />简餐',
                    icon : {
                        normal  : require('../../assets/img/icon/icon_tc.png'),
                        actived : require('../../assets/img/icon/icon_tc_hover.png'),
                    }
                },{
                    type  : COMBO_TYPE_NAMES.type3,
                    label : '特色<br />美食',
                    icon  : {
                        normal  : require('../../assets/img/icon/icon_ts.png'),
                        actived : require('../../assets/img/icon/icon_ts_hover.png'),
                    }
                },{
                    type  : COMBO_TYPE_NAMES.type2,
                    label : '精美<br />面食',
                    icon : {
                        normal  : require('../../assets/img/icon/icon_ms.png'),
                        actived : require('../../assets/img/icon/icon_ms_hover.png')
                    }
                },{
                    type  : COMBO_TYPE_NAMES.type4,
                    label : '运动<br />简餐',
                    icon : {
                        normal  : require('../../assets/img/icon/icon_yd.png'),
                        actived : require('../../assets/img/icon/icon_yd_hover.png')
                    }
                }],
                comboObjs  : null,
                status     : 0,
                oldOrderId : null,
                showGoodsImg : null,
                cover : require('../../assets/img/kfc.jpg'),
                empty : require('../../assets/img/empty.png'),
                banners : [
                    require('../../assets/img/banner.png'),
                    require('../../assets/img/banner_2.png')
                ],
                showTime : false,
                chooseComboId : null,
                orderDates : []
            }
        },
        computed : {
            curComboList() {
                return this.comboObjs ? this.comboObjs[this.selectedCategory] || [] : [];
            }
        },
        methods : {
            _showGoodsImg(img = '') {
                if(img.trim().length > 0) {
                    this.showGoodsImg = `./common/download?fileName=${img}`;
                }
            },

            _hideGoodsImg() {
                this.showGoodsImg = null;
            },

            _onRefresh() {
                setTimeout(() => {
                    this.isRefresh = false;
                }, 2000);
            },

            _setCategory(category) {
                this.selectedCategory = category;
            },

            _queryAllComboList() {
                this.isLoading = true ;
                this.isFinished = false;
                Promise.all([queryComboList(), queryOrderList(1, 100, '1')])
                    .then(args => {
                        const { list, status } = args[0],
                            r = args[1];
                        this.comboObjs = list.reduce((obj, c) => {
                            const name = COMBO_TYPE_NAMES[`type${c.comboType}`];
                            if(!obj[name]) {
                                obj[name] = [];
                            }
                            obj[name].push(c);
                            return obj;
                        }, {});
                        this.isInit = true;
                        this.status = Number(status);
                        this.isLoading = false;
                        this.isFinished = true;

                        const orders = r.rows || [];
                        if(orders && orders.length > 0) {
                            this.orderDates = orders.map(o => o.orderDate);
                            console.log(JSON.stringify(this.orderDates));
                        }
                    })
                    .catch((/*err*/) => {
                        this.isInit = true;
                        this.isLoading = false;
                        this.isFinished = true;
                        // console.error(err);
                    });
                    /*
                queryComboList()
                    .then(({ list, status }) => {
                        this.comboObjs = list.reduce((obj, c) => {
                            const name = COMBO_TYPE_NAMES[`type${c.comboType}`];
                            if(!obj[name]) {
                                obj[name] = [];
                            }
                            obj[name].push(c);
                            return obj;
                        }, {});
                        this.isInit = true;
                        this.status = Number(status);
                        this.isLoading = false;
                        this.isFinished = true;
                    })
                    .catch(() => {
                        this.isInit = true;
                        this.isLoading = false;
                        this.isFinished = true;
                        // console.error(err);
                    });*/
            },

            _confrimDate(comboId) {
                this.showTime = true;
                this.chooseComboId = comboId;
            },
            _formatDate(d) {
                let month = '' + (d.getMonth() + 1),
                    day = '' + d.getDate(),
                    year = d.getFullYear();
                
                if (month.length < 2) month = '0' + month;
                if (day.length < 2) day = '0' + day;
                return [year, month, day].join('-');
            },

            _onUserConfFrimDate(value) {
                const _date = this._formatDate(value);
                if(!this.isMod && this.orderDates.indexOf(_date) != -1) {
                    return;
                } 
                this._confrim(this.chooseComboId, _date);
            },

            _confrim(comboId, date) {
                const $loading = this.$toast.loading({
                    message     : '提交中...',
                    forbidClick : true
                });

                let fn, params;
                if(this.isMod) {
                    fn = modifyOrder;
                    params = [this.oldOrderId, comboId];
                } else {
                    fn = createOrder;
                    params = [comboId, date];
                }
                const _isMod = this.isMod, vm = this;
                fn.apply(null, params)
                    .then(() => {
                        this.showTime = false;
                        this.chooseComboId = null;
                        $loading.clear();     
                        if(_isMod) {
                            Dialog.confirm({
                                title   : '系统提示',
                                message : '修改成功,是否立即查看您的订单?'
                            }).then(()=> this.$router.push('/myOrder')).catch(()=> /*console.log('cancel')*/{});
                        } else {
                            Toast.success('点餐成功!');
                            vm.orderDates.push(date);
                        }
                    })
                    .catch(err => {
                        $loading.clear();
                        Dialog.alert({
                            message : `点餐失败,原因:${err}`,
                            theme   : 'round-button'
                        });
                    });
            },
            _checkRouter() {
                const route = this.$router.currentRoute;
                if('true' === route.query.isMod) {
                    const oid = route.query.oid || '';
                    if(oid.trim().length > 0) {
                        this.isMod = true;
                        this.oldOrderId = Number(oid);
                        const d = new Date();
                        d.setTime(route.query.date || 0);
                        this.modDate = d;
                    }
                } else {
                    this.modDate = null;
                }
                this._initPage();
            },
            _initPage() {
                const now = new Date();
                now.setHours(0, 0, 0 ,0);

                let formatter, addValue, rowHeight = 32;
                const formatFn = this._formatDate, getOrders = () => this.orderDates;
                if(this.isMod) {
                    const dd = this.modDate.getTime();
                    formatter = day => {
                        day.type = day.date.getTime() == dd ? 'selected' : 'disabled';
                        return day;
                    };
                    addValue = 0;
                } else {
                    formatter = day => {
                        const time = day.date.getTime(), nowTime = now.getTime(), orders = getOrders();
                        if(time < nowTime) {
                            day.type = 'disabled';
                        } else if(time == nowTime) {
                            if(orders.indexOf(formatFn(day.date)) != -1) {
                                day.type = 'disabled';
                                day.bottomInfo = '已点';
                            }
                        } else {
                            if(orders.indexOf(formatFn(day.date)) != -1) {
                                day.type = 'disabled';
                                day.bottomInfo = '已点';
                            }
                        }
                        return day;
                    }
                    addValue = 30;
                    rowHeight = 56;
                }
                this.calendarObj = {
                    'min-date'   : now,
                    'max-date'   : this._getMaxDate(now, addValue),
                    'row-height' : rowHeight,
                    formatter
                };
            },
            _isLeapYear(year) {
                return (year % 4 == 0 && year % 100 != 0) || year % 400 == 0;
            },

            _getMaxDate(now, addValue) {
                const month = now.getMonth(),
                     day = now.getDate(),
                     time = now.getTime();
                let value;
                if(month == 1 && this._isLeapYear(now.getFullYear())) {
                    value =  (29 - day + addValue) * ONE_DAY_TIME;
                } else {
                    value =  (MONTH_DAYS[month] - day + addValue) * ONE_DAY_TIME;
                }
                const maxDate = new Date();
                maxDate.setTime(time + value);
                return maxDate;
            }
        },
        watch : {
            '$router' : function() {
                this._checkRouter();
            } 
        },
        created() {
            this._checkRouter();
            this._queryAllComboList();
        }
    }
</script>