<template>
    <div data-view="my-orders" class="ke-fs-content-view scroll">
        <van-pull-refresh v-if="orders.length > 0" v-model="isRefresh" @refresh="_queryMyOrderList(true)">
            <van-list v-model="isLoading" finished-text="已全部加载完毕!" :finished="isFinished">
                <div v-for="order in orders" :key="order.id" class="ke-fs-history-item">
                    <h2>订单编号:{{order.orderNum != null ? order.orderNum : '待确认订单'}}</h2>
                    <div class="ke-fs-history-item__info">
                        <img :src="order.comboImg && order.comboImg.length > 0 ? `./common/download?fileName=${order.comboImg}` : ''"  />
                        <p class="ke-fs-history-item__info__comment">
                            <template v-if="'1' == order.status">
                                <van-button type="danger" size="small" @click="_cancelOrder(order.id, order.orderDate)">修改</van-button>
                                <van-button size="small" @click="_closeOrder(order.id)">取消</van-button>
                            </template>
                        </p>
                        <p class="ke-fs-history-item__info__detail">
                            <strong>{{order.comboName}}</strong>
                            <small>点单时间:{{order.orderDate}}</small>
                        </p>
                    </div>
                </div>
            </van-list>
        </van-pull-refresh>
        <van-empty v-else description="暂无点餐订单数据." />
    </div>
</template>

<script>
    import { queryOrderList, removeOrder } from '@/api/order-api';
    import { Dialog, Notify } from 'vant';
    export default {
        data() {
            return {
                isRefresh  : false,
                isLoading  : false,
                isFinished : true,
                pageNo     : 1,
                pageSize   : 10,
                orders     : [],

            }
        },
        methods : {
            _queryMyOrderList(isNewQuery = false) {
                if(isNewQuery) {
                    this.pageNo = 1;
                    this.orders = [];
                } else {
                    this.pageNo++;
                }
                this.pageNo = isNewQuery ? 1 : this.pageNo + 1;
                this.isLoading = true;
                queryOrderList(this.pageNo, this.pageSize, '0, 1')
                    .then(r => {
                        this.isRefresh = false;
                        this.isLoading = false;
                        this.isFinished = r.total - this.pageNo * this.pageSize <= 0;
                        this.orders = this.orders.concat(r.rows || []);
                    })
                    .catch(err => {
                        this.isLoading = false;
                        this.isFinished = false;
                        this.$isRefresh = false;
                        this.$notify.alert({
                            type : 'danger',
                            message : `获取我的订单数据失败,原因:${err}`
                        });
                    })
            },
            _cancelOrder(id, orderDate) {
                const ds = orderDate.split('-');
                const date = new Date();
                date.setFullYear(ds[0], ds[1] - 1, ds[2]);
                date.setHours(0, 0, 0, 0);
                Dialog.confirm({
                    title   : '系统提示',
                    message : '是否立即修改订单?'
                })
                .then(() => this.$router.push(`/list?isMod=true&oid=${id}&date=${date.getTime()}`))
                .catch(()=>{});
                
            },

            _closeOrder(orderId) {
                Dialog.confirm({
                    title   : '系统提示',
                    message : '是否立即取消订单?' 
                })
                .then(() => this._doCanncelOrder(orderId))
                .catch(() => {});
            },

            _doCanncelOrder(id) {
                removeOrder(id)
                    .then(()=> {
                        Notify({
                            type : 'success',
                            message : `订单取消成功!`
                        });
                        const index = this.orders.findIndex(v => v.id == id);
                        if(index != -1) {
                            this.orders.splice(index, 1);
                        }
                    })
                    .catch(err => Notify({
                        type : 'danger',
                        message : `取消订单失败,原因:${err}`
                    }));
            },
        },
        created() {
            this._queryMyOrderList(true);
        }
    }
</script>